import VueColor from 'vue-color'

export default {
	components: { Picker: VueColor.Chrome, Palette: VueColor.Swatches },
	props: {
		currentColor: {
			type: String,
		},
	},
	data() {
		return {
			newColor: '',
		}
	},
	methods: {
		getColor() {
			return this.newColor
		},
	},
	mounted() {
		this.newColor = this.currentColor
	},
}
